'use strict';

/**
* Inverse gamma distributed pseudorandom numbers.
*
* @module @stdlib/random-base-invgamma
*
* @example
* import invgamma from '@stdlib/random-base-invgamma';
*
* var v = invgamma( 2.0, 3.0 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-invgamma';
* var invgamma = factory( 3.0, 2.0, {
*     'seed': 297
* });
*
* var v = invgamma();
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-invgamma';
* var invgamma = factory({
*     'seed': 297
* });
*
* var v = invgamma( 3.0, 2.0 );
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
